.gallery {
  padding: 20px 20px 40px 20px;

  @media screen and (min-width: 768px) {
    padding: 60px 75px 60px 75px;
  }

  @media screen and (min-width: 1024px) {
    padding: 60px 71px 60px 71px;
  }
}

.gallery__list {
  list-style: none;
  display: block;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-top: -30px;
    margin-left: -30px;
  }

  @media screen and (min-width: 1024px) {
    display: flex;
    flex-wrap: wrap;
    margin-top: -30px;
    margin-left: -30px;
  }
}

.gallery__item {
  
//   max-width: 280px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-basis: calc(100% / 2 - 30px);
    margin-top: 30px;
    margin-left: 30px;
    // max-width: 439px;
  }

  @media (min-width: 1024px) {
    flex-basis: calc(100% / 3 - 30px);
    margin-top: 30px;
    margin-left: 30px;
  }
}

.gallery__info-list {
  list-style: none;
}

.gallery__image-wrapper {
  object-fit: contain;
  margin-bottom: 10px;
  flex-grow: 1;
//   width: 100%;
//   height: 402px;
}

.gallery__image-wrapper img{
  overflow: hidden;
  border-radius: 5px;
}

.gallery__info-genre {
  display: flex;
  color: rgba(255, 107, 8, 1);
  margin: 5px 5px 5px 5px;
}

.gallery__info-name {
  margin-left: 5px;
}

.gallery__info-item{
  margin-right: 5px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  
}

.gallery__info-rating {
  display: none;
  padding: 3px 10px 1px 10px;
  background-color: rgb(251, 152, 81);
  color: rgba(255, 255, 255, 1);
  
  border-radius: 5px;
  font-size: 12px;
}

.is-hidden {
  display: none;
}

.blur-up{
  filter: blur(5px);
  transition: filter 400ms;
}

.lazyloaded{
  filter: blur(0);
}

.scroll-btn-hide{
  opacity: 0;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}